
.hearder {
  width: 100%;
  height: 200px;
  background: red url(../../assets/img/02.jpg) no-repeat;
  background-size: cover;
  /* opacity: 0.5; */
}
.headerImg {
  width: 8rem;
  height: 10rem;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.headerImg img {
  display: block;
  border-radius: 50%;
  width: 8rem;
  height: 8rem;
}
.headerImg h6 {
  font-weight: bold;
  color: #FF0000;
  text-align: center;
  margin: 10px;
  font-size: 20px;
}




