
.footer {
  width: 100%;
  height: 50px;
  background-color: #fff;
  bottom: 0;
  left: 0;
  border-top: 1px solid red;
}
.footer .circle {
  bottom: 5rem;
  right: 0.8rem;
  background-color: rgba(119, 136, 153, 0.5);
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  color: #0e82e8;
  text-align: center;
}
.footer .animate {
  -webkit-animation: mycrilce 2s infinite alternate;
          animation: mycrilce 2s infinite alternate;
}
@-webkit-keyframes mycrilce {
0% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1)
}
}
@keyframes mycrilce {
0% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1)
}
}




